.nb-sansita {
    font-family: 'Rajdhani', sans-serif;
    font-weight: 600;
}
.nbl-pt-sans {
    font-family: 'Rajdhani', sans-serif;
    font-weight: 500;
}

.btn-lifted-border {
    border-width: 3px;
}

.btn-indent-right {
    margin-right: 10px;
}

.active {
    border-bottom: 1px solid #ffffff;
}

.navlink {
    color: #ffffff;
}

.navbar {
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
    background-color: linear-gradient(270deg, #343a40, #484e53); /*#262a2e;*/
    box-shadow: none;
    transition: all 0.2s;
}

.navbar.top-nav-collapse {
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: #262a2e;
    box-shadow: 0 0.0625rem 0.375rem 0 rgba(0, 0, 0, 0.3);
}

.header {
    padding-top: 8rem;
	padding-bottom: 0rem;
    background: linear-gradient(270deg, #343a40, #484e53);
    height: auto;
}

.header .text-container {
	margin-bottom: 4rem;
}

.image-figure-config {
    border: 3px solid #ffffff;
    box-shadow: 0px 0px 5px #000000;
    width: 70%;
    height: auto;    
}

.h2-header-config {
    color: #ffffff;
    font-family: 'Kanit', sans-serif;
    font-weight: 400;
    text-align: center;
}

.p-desc {
    text-align: center;
    color: #ffffff;
    margin-top: 1rem;
}

.p-desc-v2 {
    font-family: 'Rajdhani', sans-serif;
    font-weight: 400;
    font-size: 14pt;    
    margin-top: 10px;
    color: #cac8c8;
}

.p-desc-v3 {
    font-family: 'Rajdhani', sans-serif;
    font-weight: 400;
    font-size: 14pt;
    margin-top: 10px;
    color: #cac8c8;
    text-align: center;
}

.b-info {
    font-family: 'Rajdhani', serif;
    font-weight: 500;
    font-size: 23pt;
    color: #ffffff;
}

.button-indent-right {
    margin-right: 10px;
}

.button-circle {
    border-radius: 50%;
}

.button-mtop-10 {
    margin-top: 10px;
}

.h2-header-title {
    color: #ffffff;
    font-family: 'Rajdhani', serif;
    font-weight: 500;
    text-align: center;
}

.div-border-about {
    border: 1px solid #ffffff;
    width: 150px;
    margin: auto;
    margin-bottom: 20px;
    background: #ffffff;
}

.list-group-item {
    background: transparent;
    font-family: 'Rajdhani', serif;
    font-weight: 400;
    font-size: 14pt;
    color: #ffffff;
}

.l-item {
    font-size: 13pt;
    color: #cac8c8;
    font-style: normal;
}

.button-hire {
    width: 250px;
}
